import React from "react"
import Link from "gatsby-link"
import Menu from 'react-burger-menu/lib/menus/slide'
// import ThemeSwitch from './ThemeSwitch'
import FontAwesomeIcon from '@fortawesome/react-fontawesome'
import faAngleRight from '@fortawesome/fontawesome-free-solid/faAngleRight'


class SideMenu extends React.Component {
  render () {
    return (
        <Menu isOpen={ false }>
          <h6></h6>
          {/* <ThemeSwitch /> */}
          <div className="menu-category">
          <h3 className="menu-category-name"></h3>
            <div className="menu-category-items-h">
              <Link to="/" className="menu-top-item"></Link>
              <Link to="/bmi" className="menu-top-item">BMI</Link>
            </div>
          </div>
          <div className="menu-category">
            <h3 className="menu-category-name"> </h3>
            <div className="menu-category-items">
              <Link to="/bodypart/shoulders" className="menu-category-item"> <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/bodypart/chest" className="menu-category-item"> <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/bodypart/biceps" className="menu-category-item"> <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/bodypart/triceps" className="menu-category-item"> <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/bodypart/back" className="menu-category-item"> <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/bodypart/legs" className="menu-category-item"> <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/bodypart/cardio" className="menu-category-item"> <FontAwesomeIcon icon={faAngleRight} /></Link>
            </div>
          </div>
          <div className="menu-category">
            <h3 className="menu-category-name"></h3>
            <div className="menu-category-items">
              <Link to="/inventory/Dumbbells" className="menu-category-item"> <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/inventory/EzStange" className="menu-category-item">EZ- <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/inventory/Stange" className="menu-category-item"> <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/inventory/Trx" className="menu-category-item">TRX- <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/inventory/Smit" className="menu-category-item">  <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/inventory/Scott" className="menu-category-item">  <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/inventory/Scott" className="menu-category-item">- <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/inventory/Scott" className="menu-category-item"> <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/inventory/ownweight" className="menu-category-item">  <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/inventory/Scott" className="menu-category-item"> <FontAwesomeIcon icon={faAngleRight} /></Link>
            </div>
          </div>
          <div className="menu-category">
            <h3 className="menu-category-name"> </h3>
            <div className="menu-category-items">
              <Link to="/type/Base" className="menu-category-item"> <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/type/Isolated" className="menu-category-item"> <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/stats/efficiency" className="menu-category-item">   <FontAwesomeIcon icon={faAngleRight} /></Link>
              <Link to="/stats/injury" className="menu-category-item">   <FontAwesomeIcon icon={faAngleRight} /></Link>
            </div>
          </div>
        </Menu>
    );
  }
}

export default SideMenu;



