import React from "react";
import FontAwesomeIcon from '@fortawesome/react-fontawesome';
import faGithub from '@fortawesome/fontawesome-free-brands/faGithub';
import faYoutube from '@fortawesome/fontawesome-free-brands/faYoutube';
import faSoundcloud from '@fortawesome/fontawesome-free-brands/faSoundcloud';
import faInstagram from '@fortawesome/fontawesome-free-brands/faInstagram';
import faFacebook from '@fortawesome/fontawesome-free-brands/faFacebook';
import faEnvelope from '@fortawesome/fontawesome-free-regular/faEnvelope';

class Footer extends React.Component {
  render () {
    return (
      <footer className="footer">
        <div className="app-container">
          <div className="footer-container">
            <div className="footer-legal">Fitness Map 2018 | Designed by @airsyn</div>
            <div className="footer-social">
              <a href="mailto:pojar.vladislav@gmail.com" className="footer-link"><FontAwesomeIcon icon={faEnvelope} /></a>
              <a href="https://github.com/airsyn" className="footer-link"><FontAwesomeIcon icon={faGithub} /></a>
              <a href="https://www.youtube.com/channel/UCJ0Y21f7dAdBWZmB6iuMWjQ?view_as=subscriber" className="footer-link"><FontAwesomeIcon icon={faYoutube} /></a>
              <a href="https://soundcloud.com/airsyn" className="footer-link"><FontAwesomeIcon icon={faSoundcloud} /></a>
              <a href="https://instagram.com/airsyn" className="footer-link"><FontAwesomeIcon icon={faInstagram} /></a>
              <a href="https://facebook.com/airsyn" className="footer-link"><FontAwesomeIcon icon={faFacebook} /></a>
            </div>
          </div>
        </div> 
      </footer>
   );
  }
}

export default Footer;



